.bodyBox{
    padding-top:3.6rem;
    min-height:830px;
}
.bodyLeft{
    position:fixed;
    width:18%;
    top: 3.6rem;
    left: 0;
    bottom: 0;
    font-size: 16px;
    background-color: #fff;
    box-sizing: border-box;
    border-right: 1px solid #eaecef;
    overflow-y: auto;
}
.leftOneList{
    padding:1.5rem 0;
}
.leftTitle{
    padding:.35rem 1.5rem .35rem 1.25rem;
    margin:0;
    font-size: 1.1em;
    font-weight: 700;
}
.leftOneList ul {
    padding: 0;
    margin: 0;
    list-style-type: none;
}
.leftOneList a{
    font-size: 1em;
    font-weight: 400;
    color: #2c3e50;
    border-left: .25rem solid transparent;
    line-height: 1.4;
    width: 100%;
    box-sizing: border-box;
}
.leftTwoList li{
    padding: .4rem 1rem .4rem 1.25rem;
}
.leftOneList li{
    padding: .6rem 1rem .6rem 1.25rem;
    font-size: 1em;
    font-weight: 400;
    color: #2c3e50;
    border-left: .25rem solid transparent;
    line-height: 1.4;
    width: 100%;
    box-sizing: border-box;
    cursor:pointer;
}
.leftOneList li:hover{
    color:#40a9ff;
}
.bodyRight{
    width:84%;
    display: inline-block;
    margin-left:16%;
    padding:30px 0 50px 0;
    box-sizing: border-box;
}
.articalTitle{
    text-align: center;
}
.leftOneList .active{
    color:#40a9ff;
}
.articalBody{
    min-height:500px;
    .articalInfo{
        margin-bottom:30px;
    }
}
.rightBox{
    max-width: 800px;
    margin:0 auto;
}
.ql-syntax{
    padding:10px;
    border-radius:10px;
    background-color:#f6f6f6;
}
.articalBody img{
    width:100%;
}